<template>
  <md-card>
    <md-card-actions v-md-ink-ripple>
      <div class="md-subhead">
        <span>scroll-container</span>
        <span></span>
        <span>/</span>
        <span></span>
      </div>
      <md-button class="md-icon-button"
                 target="_blank"
                 href="https://github.com/surmon-china/vue-awesome-swiper/blob/master/examples/32-scroll-container.vue">
        <md-icon>code</md-icon>
      </md-button>
    </md-card-actions>
    <md-card-media>
      <!-- swiper -->
      <swiper :options="swiperOption">
        <swiper-slide class="text">
          <div class="content">
            <h4 class="title"></h4>
            <p></p>
            <p></p>
            <p>
              <blockquote>  P61
              P86
              P114
              </blockquote>
            </p>
            <p></p>
            <br>
            <p></p>
            <br>
            <p></p>
          </div>
        </swiper-slide>
        <div class="swiper-scrollbar" slot="scrollbar"></div>
      </swiper>
    </md-card-media>
  </md-card>
</template>

<script>
  export default {
    data() {
      return {
        swiperOption: {
          scrollbar: '.swiper-scrollbar',
          direction: 'vertical',
          slidesPerView: 'auto',
          mousewheelControl: true,
          freeMode: true
        }
      }
    }
  }
</script>

<style lang="scss">
  .swiper-slide.text {
    font-size: 18px!important;
    text-align: left!important;
    height: auto;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 30px;

    > .content {

      > .title {
        margin: 0;
        text-align: center;
        margin-bottom: .75em;
      }

      > p,
      blockquote {
        line-height: 2;
        text-indent: 2em;
      }

      blockquote {
        background-color: #ddd;
        margin-left: 2em;
      }
    }
  }
</style>



